    setSelectedOptionjwtcategory = (val) =>{
      this.setState({
        jwtcategories: val
      });
      setTimeout(function(){
     //alert(JSON.stringify(val));
     //alert(JSON.stringify(this.state.all_channels));
     var channelstv_jwt = [] 
      this.state.all_channels.forEach(data => {
        if(data.category === val){
        channelstv_jwt.push({
          id: data.id,
          denumire: data.denumire,
          adresa: data.adresa,
          category: data.category,
          description: data.description,
          subcategory: data.subcategory,
          image: data.image,
          year: data.year,
          imdb: data.imdb,
          status: data.status
        })
      }
    })
    if(parseInt(val) === 0){
      channelstv_jwt = this.state.all_channels
    }
    this.setState({ channels: channelstv_jwt })
    this.setCarouselItems()
    }.bind(this), 300);
    }

    setCategory_jwt_category = () =>{
      var curtime = moment().format("DD/MM/YYYY HH:mm:ss");

      firebase.database().ref("/channels_jwt_category").remove()
      this.state.jwtcategory.forEach(child => {
      var refdb = firebase.database().ref('/channels_jwt_category').push();
      refdb = refdb.toString().slice(-20);
      let updateData = firebase.database().ref("/channels_jwt_category").child(refdb);
      updateData.child("id").set(child.id);
      updateData.child("title").set(child.title);
      updateData.child("description").set(child.description);
      updateData.child("time").set(curtime);
      });
    }

    setCategory_jwt_subcategory = () =>{
      var curtime = moment().format("DD/MM/YYYY HH:mm:ss");

      firebase.database().ref("/channels_jwt_subcategory").remove()
      this.state.jwtsubcategory.forEach(child => {
      var refdb = firebase.database().ref('/channels_jwt_subcategory').push();
      refdb = refdb.toString().slice(-20);
      let updateData = firebase.database().ref("/channels_jwt_subcategory").child(refdb);
      updateData.child("id").set(child.id);
      updateData.child("title").set(child.title);
      updateData.child("description").set(child.description);
      updateData.child("time").set(curtime);
      });
    }

    setCarouselItems = () =>{
      setTimeout(function(){ 
      var i=0;
      var j=0;
      var carouselitemss=[];
      var carouselitemssub=[];
      this.state.channels.forEach(data => {
        carouselitemssub.splice(j,0, {
          id: data.id,
          denumire: data.denumire,
          adresa: data.adresa,
          category: data.category,
          description: data.description,
          subcategory: data.subcategory,
          image: data.image,
          year: data.year,
          imdb: data.imdb,
          status: data.status
        })
        
        j=j+1;
        if(j===4){
          carouselitemss.splice(i,0,{carouselitemssub});
          carouselitemssub=[];
          i=i+1;
          j=0;
        }
        this.setState({
          carouselitems: carouselitemss
        });
      })
      //setTimeout(function(){ 
      //  console.log(JSON.stringify(this.state.carouselitems));
      //}.bind(this), 200);
      }.bind(this), 1200);
    }

    selectChange = (val) =>{
      //alert(JSON.stringify(val));
      //alert(JSON.stringify(this.state.selectoptions));
      //alert(val.value);
      this.state.channels.forEach(function(data) {
        if(data.id === val.value){
          this.setState({
            playlist: data.adresa
          });
        }
      }.bind(this))
    }

    carouselselectchannel = (val) =>{
      //alert(JSON.stringify(val));
      this.setState({
        playlist: val,
      });

      this.setSelectedOption(val);

      setTimeout(function(){ 
      this.state.channels.forEach(data => {
        if(data.adresa === this.state.playlist){
          this.setState({
            rezumat: data.description
          });
        }
      });
      }.bind(this), 200);
    }

